import LocalizationBase,{Lang, DefaultLang } from "./LocalizationBase";



const { ccclass, property, requireComponent } = cc._decorator;

@ccclass
@requireComponent(cc.Label)
export default class LocalizationLabelString extends LocalizationBase<string> {
    

    @property()
    private brasil: string = '';

    @property()
    private columbia: string = '';

    private label: cc.Label = null;

    protected onLoad() {
        super.onLoad();
        this.defaultLang = DefaultLang.brasil;
        this.curLang = Lang.Brasil;
        this.label = this.node.getComponent(cc.Label);
    }

    protected onLangChanged() {
        if (this.label) this.label.string = this.get();
    }

}
