import PopupBase from "../cocos-eazax-kit-master/components/popups/PopupBase";

const { ccclass, property } = cc._decorator;

@ccclass
export default class jiesuanLayer extends PopupBase<jiesuanLayerOptions> {

    @property(cc.Sprite)
    private title: cc.Sprite = null;

    @property(cc.Label)
    private winnumLabel: cc.Label = null;
    @property(cc.Label)
    private lossnumLabel: cc.Label = null;

    // @property(cc.Node)
    // private confirmBtn: cc.Node = null;

    protected onLoad() {
        this.registerEvent();
    }

    protected onDestroy() {
        this.unregisterEvent();
    }

    private registerEvent() {
        //this.confirmBtn.on(cc.Node.EventType.TOUCH_END, this.onConfirmBtnClick, this);
    }

    private unregisterEvent() {
        //this.confirmBtn.targetOff(this);
    }

    protected init() {

    }

    protected updateDisplay(options: jiesuanLayerOptions): void {
        if(Number(options.num) > 0){
            this.title.spriteFrame = options.parent.getRes("game/WIN",cc.SpriteFrame)
            this.winnumLabel.string = (options.num.toString()).replace(/[\+\-]/g, '')
            this.winnumLabel.node.active = true
            this.lossnumLabel.node.active = false
        }else{
            this.title.spriteFrame = options.parent.getRes("game/LOSE",cc.SpriteFrame)
            this.lossnumLabel.string = (options.num.toString()).replace(/[\+\-]/g, '')
            this.winnumLabel.node.active = false
            this.lossnumLabel.node.active = true
        }

    }

    // protected onConfirmBtnClick() {
    //     this.options.confirmCallback && this.options.confirmCallback();
    //     this.hide();
    // }

}

/** 结算选项 */
export interface jiesuanLayerOptions {
    num: string;
    parent: any;
    //confirmCallback: Function;
}
